import React from 'react'
import Button from 'react-bootstrap/Button';
import "./cartouche.css";

const Cartouche = ({byWordOrUser}) => {
  return (
    <div id="cartoucheContainer" className="">
        {!byWordOrUser ? 
          <div className="">
            <div className="page-header header-cartouche">
              <div className="col-sm-10"><img className="imgLogo" data-key="" src={window.location.protocol + `//` + window.location.hostname +`/coco_bot/back_bot/public/logo.png`} alt="Logo" /></div>
              <div className="headerAccueil">
                <Button variant="secondary">Home</Button>
                <Button variant="secondary">About</Button>
              </div>
            </div>
          </div> :
          <div id="cartoucheContainerWord"></div>
        }
    </div>
  )
}

export default Cartouche